import { Injectable } from '@angular/core';
import * as moment from 'moment';

import { NewsRubric } from './news-rubric';
import { ListItemsService } from '../list-items-service';


@Injectable({
  providedIn: 'root'
})
export class NewsRubricsService extends ListItemsService<NewsRubric>  {

  getListRelativePath(): string {
    return '';
  }

  getListTitle(): string {
    return 'Рубрики новостей';
  }

  getSelect(): string {
    return 'Id,Title';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['Title', true]];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): NewsRubric {
    return { id: item.Id, title: item.Title };
  }

  convertEntityToListItem(entity: NewsRubric) {
    return { Id: entity.id, Title: entity.title };
  }

  getMockItems(): NewsRubric[] {
    return [{ id: 1, title: 'Рубрика 1' }, { id: 2, title: 'Рубрика 2' }, { id: 3, title: 'Рубрика 3' }];
  }

  isMock(): boolean {
    return false;
  }
}
