import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';

import { ListItemsService } from '../list-items-service';
import { NewsView } from './newsView';

@Injectable({
  providedIn: 'root'
})
export class NewsViewsService extends ListItemsService<NewsView>  {

  getListTitle(): string {
    return 'Просмотры новостей';
  }

  getListRelativePath(): string {
    return '/Lists/slNewsComments';
  }

  getSelect(): string {
    return 'Id,Title,slLookupNews/Id,slLookupNews/Title,LikedBy/Id,LikesCount';
  }

  getExpand(): string {
    return 'slLookupNews,LikedBy';
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): NewsView {
    const view: NewsView = {
      id: item.Id,
      title: item.Title,
      newsLookupId: null,
      count: item.LikesCount,
      userIds: item.LikedBy && item.LikedBy.map(l => l.Id) || []
    };
    if (item.slLookupNews) {
      view.newsLookupId = item.slLookupNews.Id;
    }
    return view;
  }

  convertEntityToListItem(entity: NewsView) {
    return {
      Id: entity.id,
      Title: entity.title,
      slLookupNewsId: entity.newsLookupId
    };
  }

  getMockItems(): NewsView[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  getItemsByNewsId(ids: number[]): Observable<NewsView[]> {
    if (ids && ids.length > 0) {
      let filter = '';
      for (const id of ids) {
        filter += `slLookupNews/Id eq ${id} or`;
      }
      filter = filter.slice(0, -3);
      return this.getItems({ filter: filter, top: ids.length });
    } else {
      return of([]);
    }
  }
}
