import { Injectable } from '@angular/core';
import { NewsComment } from './newsComment';
import { ListItemsService } from '../list-items-service';
import { Observable, of } from 'rxjs';

import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class NewsCommentsService extends ListItemsService<NewsComment> {


  getListTitle(): string {
    return 'Комментарии к новостям';
  }

  getListRelativePath(): string {
    return '/Lists/slNewsComments';
  }

  getSelect(): string {
    return 'Id,Title,slComment,slLookupNews/Id,slLookupNews/Title,Created,Author/Title,Author/Name,Author/Id';
  }
  getExpand(): string {
    return 'slLookupNews,Author';
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): NewsComment {
    const comment: NewsComment = {
      id: item.Id,
      title: item.Title,
      comment: item.slComment,
      newsLookupId: null,
      name: item.Author.Title,
      photo: `/_layouts/15/userphoto.aspx?size=M&username=${item.Author.Name}`,
      dateTitle:  moment(item.Created).calendar()
    };
    if (item.slLookupNews) {
      comment.newsLookupId = item.slLookupNews.Id;
    }
    if (item.Author) {
      comment.authorId = item.Author.Id;
      comment.authorTitle = item.Author.Title;
    }
    return comment;
  }

  convertEntityToListItem(entity: NewsComment) {
    return {
      Id: entity.id,
      Title: entity.title,
      slComment: entity.comment,
      slLookupNewsId: entity.newsLookupId
    };
  }

  getMockItems(): NewsComment[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  getItemsByNewsId(ids: number[]): Observable<NewsComment[]> {
    if (ids && ids.length > 0) {
      let filter = '';
      for (const id of ids) {
        filter += `slLookupNews/Id eq ${id} or`;
      }
      filter = filter.slice(0, -3);
      return this.getItems({ filter: filter, top: ids.length });
    } else {
      return of([]);
    }
  }
}
